<div class="contacts p-mb-6"><h3 class="p-text-center">I'm here because I love to work with interesting people.</h3><hr><Tree :value="nodes" @click="clickTree" :expandedKeys="expandedKeys" :loading="loading" @node-expand="onNodeExpand"><template #url="slotProps"><a :href="slotProps.node.data" target="_blank" rel="nofollow noindex noopener">{{slotProps.node.label}}</a></template><template #default="slotProps"><b>{{slotProps.node.label}}</b></template></Tree></div>